@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans Regular"),
    local("OpenSans-Regular"),
    url("../fonts/opensans.woff2") format("woff2"),
    url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans Bold"),
    local("OpenSans-Bold"),
    url("../fonts/opensansbold.woff2") format("woff2"),
    url("../fonts/opensansbold.woff") format("woff");
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  margin: 0 auto;
  max-width: 260px;
  padding-top: 14px;
  padding-bottom: 13px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 118%;
  color: var(--light-type);
  text-decoration: none;
  background-color: transparent;
  border-style: solid;
  border-color: var(--basic-border);
  border-width: 2px;
  cursor: pointer;
  font-family: inherit;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: -0.5px;
  transition: 0.15s;

  &:hover,
  &:focus {
    color: var(--contrast-type);
    background-color: var(--button-hover-background);
  }

  &:active {
    color: var(--contrast-type-down);
  }
}
